.videoCard{
    margin-bottom: 40px;
    width: 270px;

}
.videoCard__thumbnail{
    height: 140px;
    width: 250px;
    
}
.videoCard__info{
    display: flex;
    margin-top: 10px;
    margin-right: 30px;
}
.video__text{
    margin-left: 15px;
}
.video__text > h4 {
    font-size: 14px;
    margin-bottom: 5px;
}
.video__text > p {
    font-size: 14px;
    color: gray;
}

.videoCard__avatar{
    height: 30px !important;
    width: 30px !important;
}